import React from 'react';
import logo from './../../logo.svg';
import './Home.css';
import {NavLink} from "react-router-dom";

function Home() {
    return (
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo" />
                <NavLink to="/game">Start</NavLink>
            </header>
        </div>
    );
}

export default Home;
