import React from 'react';
import './Button.css';


class Button extends React.Component {
    state = {
        className: null,
    };


    async componentDidMount() {
        this.setState({vision: this.props.vision})

    }


    render() {
        return (
            <div>
                <button className={this.state.vision ? "button_" + this.props.color : "button"}
                        onClick={
                            () => {
                                this.setState({vision: true});
                                this.props.click(this.props.id, this.props.name, this.props.color);
                                console.log("click");
                            }

                        }>
                    {this.props.name}
                </button>
            </div>
        )
    };
}


export default Button;