import React from 'react';
import './Header.css';
import Button from "../Button/Button.js";


class Header extends React.Component {
    state = {
        className: null,
    };


    async componentDidMount() {
        this.setState({vision: this.props.vision})

    }


    render() {
        return (
            <div className="header">
                <h1 id="sitename">Soltivka.ho.ua</h1>
                <div className="header-buttons">
                    <a className="header_button" href={"../../../index.html"}>Home</a>
                    <a className="header_button" href={"../../../index.html"}>Personal projects</a>
                    <a className="header_button" href={"../../../index.html"}>My works</a>
                </div>
            </div>
        )
    };
}


export default Header;