import React from 'react';
import './QuestionCard.css';
import Button from "../Button/Button.js";


const getRandom = function (max) {
    return Math.floor(Math.random() * Math.floor(max));
};
let currentQuestion = 0;
let questions;

class QuestionCard extends React.Component {
    state = {
        difficultyChoosen: false,  //CHANGE ON FALSE
        difficultyID: null,
        question: null,
        correctAnswer: null,
        answer: null,
        buttons: [],
        currentQuestion: 0,
        gameOver: false,
        questions: [],
        correctAnswersCount:0,
        incorrectAnswersCount:0,
    };


    chooseDifficulty = async (id, name, color) => {
        await this.setState({difficultyChoosen: true});
        await this.setState({difficultyID: id});
        await this.setState({difficultyColor: color});
        console.log(this.state.difficultyColor)
        await this.setState({difficultyText: name});
        console.log("Choosen difficulty:" + this.state.difficultyID);
        this.getQuestions(this.props.choosenCategoryID, this.state.difficultyText);
    };
    getQuestions = async function (category, difficulty) {

        let questionsAmount = 0;
        let difficultyName = difficulty.toLowerCase();
        const questionsAmount_URL = `https://opentdb.com/api_count.php?category=${category}`;
        let questionsAmount_response = await fetch(questionsAmount_URL);
        let questionsAmount_json = await questionsAmount_response.json();
        let maxQuestionsAmount = questionsAmount_json.category_question_count["total_" + difficultyName + "_question_count"];
        questionsAmount = 20 > maxQuestionsAmount ? maxQuestionsAmount : 20;
        console.log("questionsAmount" + questionsAmount);

        const questions_URL = `https://opentdb.com/api.php?amount=${maxQuestionsAmount}&category=${category}&difficulty=${difficultyName}`;
        let allQuestions_response = await fetch(questions_URL);
        let allQuestions_json = await allQuestions_response.json();
        questions = allQuestions_json.results;
        this.setState({questions});
        this.setQuestion();

    };
    setQuestion = () => {
        this.setState({question: this.state.questions[this.state.currentQuestion].question});
        this.setState({correctAnswer: this.state.questions[this.state.currentQuestion].correct_answer});
        this.setState({incorrectAnswers: this.state.questions[this.state.currentQuestion].incorrect_answers});
        this.setButtons();
    };
    setButtons = function () {
        let buttons = [];
        this.setState({buttons:null})
        for (let i = 0; i < this.state.incorrectAnswers.length; i++) {
            let button = <Button
                name={this.state.incorrectAnswers[i]}
                color={"red"}
                key={i + 1}
                click={this.checkAnswer}
                vision={false}
            />;
            buttons.push(button);
        }
        let button = <Button
            name={this.state.correctAnswer}
            color={"green"}
            key={0}
            click={this.checkAnswer}
            vision={false}
        />;
        buttons.push(button);
        buttons = buttons.sort(function () {
            return Math.random() - 0.5;
        });
        this.setState({buttons:buttons});
        console.log(this.state.buttons)
    }


    checkAnswer = async (id, color, answer) => {
        currentQuestion = await currentQuestion + 1;
        this.setState({currentQuestion});
        if (this.state.currentQuestion > this.state.questions.length) { //проверка: закончились ли вопросы ?
            this.setState({gameOver: true});
            console.log('gameover');
        }
        if (answer == this.state.correctAnswer) {
            console.log('question ' + currentQuestion + ' is correct');
            this.setState({correctAnswersCount: this.state.correctAnswersCount+1})
        } else {
            console.log('wrong' + this.state.correctAnswer + "!=" + answer);
            this.setState({incorrectAnswersCount:this.state.incorrectAnswersCount+1});
        }

        setTimeout(this.setQuestion, 1000);


    };


    render() {
        if (this.state.difficultyChoosen) {
            return (
                <div className="gameContainer">
                    <div className="statsContainer">
                        <b>Choosen category: {this.props.categoryName}</b>
                        <b>Choosen difficulty:
                            <b className={this.state.difficultyColor}> {this.state.difficultyText}</b>

                        </b>
                    </div>
                    <div className="statsContainer statsContainerRight">
                        <b>Current question:{this.state.currentQuestion+1}/{this.state.questions.length+1}</b>
                        <b>Correct/incorrect answers: {this.state.correctAnswersCount}/{this.state.incorrectAnswersCount} </b>

                    </div>
                    <div className="questionContainer">
                        <h2 className="question">{this.state.question} </h2>
                        <div className="buttons_container">{this.state.buttons} </div>
                    </div>


                </div>
            )
        }
        return (
            <div className="difficultyContainer">
                <h2 className="question">Please choose difficulty of Questions:</h2>
                <div className="buttons_container">
                    <Button vision={true} name="Easy" color="green" id="1" click={this.chooseDifficulty}/>
                    <Button vision={true} name="Medium" color="orange" id="2" click={this.chooseDifficulty}/>
                    <Button vision={true} name="Hard" color="red" id="3" click={this.chooseDifficulty}/>
                </div>

            </div>
        )
    }
}

export default QuestionCard;