import React from 'react';
import CategoryItem from "../CategoryItem/CategoryItem.js";
import QuestionCard from "../QuestionCard/QuestionCard.js";
import './Game.css'


const getRandom = function (max) {
    return Math.floor(Math.random() * Math.floor(max));
};


let cards = [];
let alreadyMount = false;
const getImage = async (searchTerm) => {
    searchTerm = searchTerm.split(' ').join('+').split(':').join('');
    searchTerm = searchTerm.replace('Entertainment', '');
    searchTerm = searchTerm.replace('General', '');
    searchTerm = searchTerm.replace('&', '');
    searchTerm = searchTerm.replace('Science', '');
    searchTerm = searchTerm.replace('Manga', '');

    let images_URL = `https://pixabay.com/api/?key=15140358-a7c7469e9f7b84415745cbb2c&q=${searchTerm}&image_type=photo`;
    let response = await fetch(images_URL);
    let obj_images;
    if (response.ok) {
        obj_images = await response.json();
    } else {
        alert("Ошибка HTTP: " + response.status);
    }
    let arr_images = obj_images.hits;
    let image_id = getRandom(arr_images.length);
    let image_obj = arr_images[image_id];
    let image_URL = image_obj.webformatURL;
    let splittedImage = image_URL.split('_');
    splittedImage[1] = "180.jpg";
    image_URL = splittedImage.join('_');
    return image_URL;
};


class Game extends React.Component {
    state = {
        data: {},
        error: null,
        categories: [],
        cards: [],
        categoryChoosen: false, // CHANGE THIS
        categoryName: null,   // CHANGE THIS TO NULL
        choosenCategoryId: null,// CHANGE THIS TO NULL
    };

    fetchData = async () => {
        let categories_URL = 'https://opentdb.com/api_category.php';
        let response = await fetch(categories_URL);
        if (response.ok) {
            const data = await response.json();
            const categories = data.trivia_categories;
            this.setState({categories});
        } else {

            this.setState({
                error: "Ошибка HTTP: " + response.status
            });
        }
    };
    chooseCategory = async (categoryClicked, categoryName) => {
        await this.setState({categoryChoosen: true});
        await this.setState({categoryName: categoryName});
        await this.setState({choosenCategoryId: categoryClicked});
        console.log(this.state.categoryName + '   ' + this.state.choosenCategoryId + '  categoryChoosen:' + this.state.categoryChoosen)
    };

    getImages = async ()=> {
        for (let i=0;i<this.state.categories.length; i++){
            let img_url = await getImage(this.state.categories[i].name);
            let card = <CategoryItem
                name={this.state.categories[i].name}
                text={this.state.categories[i].name}
                id={this.state.categories[i].id}
                img_URL={img_url}
                key={this.state.categories[i].id}
                click={this.chooseCategory}/>;
            cards.push(card);
        }

        this.setState({cards});
    };


    async componentDidMount() {
        if (alreadyMount === false) {
            console.log(alreadyMount);
            await this.fetchData();
            await this.getImages();
            alreadyMount = true;
            console.log(alreadyMount);
        } else {
            this.setState({cards});
        }

    };


    render() {
        if (this.state.error) {
            return (<div>
                    <p>Error: {JSON.stringify(this.state.error, null, 2)}</p>
                    <CategoryItem text='some another text'/>
                </div>
            )

        }

        if (this.state.categoryChoosen) {
            return (
                <div className="gameContainer">
                    <QuestionCard choosenCategoryID={this.state.choosenCategoryId}
                                  categoryName={this.state.categoryName}/>
                </div>
            )
        }

        return (
            <div className="gameContainer">
                <h1 className="title">Hello! Choose category of questions.</h1>
                <div className="gameContainer">
                    <div className="cardsContainer">
                        {this.state.cards}
                    </div>


                </div>
            </div>

        )
    }
}

export default Game;
